import axios from "axios"
import { USER_DETAILS_FAIL, 
    USER_DETAILS_REQUEST, 
    USER_DETAILS_SUCCESS, 
    USER_LOGIN_FAIL, 
    USER_LOGIN_REQUEST, 
    USER_LOGIN_SUCCESS, 
    USER_LOGOUT,
    USER_REGISTER_FAIL,
    USER_REGISTER_REQUEST, 
    USER_REGISTER_SUCCESS,
    USER_UPDATE_PROFILE_FAIL,
    USER_UPDATE_PROFILE_REQUEST,
    USER_UPDATE_PROFILE_SUCCESS,
    USER_DETAILS_RESET,
    USER_LIST_REQUEST,
    USER_LIST_SUCCESS,
    USER_LIST_FAIL,
    USER_LIST_RESET,
    USER_DELETE_REQUEST,
    USER_DELETE_SUCCESS,
    USER_DELETE_FAIL,
    USER_UPDATE_REQUEST,
    USER_UPDATE_SUCCESS,
    USER_UPDATE_FAIL,
} from "../constants/userConstants"

import { ORDER_LIST_MY_RESET } from '../constants/orderConstants'

//request for login and getting token  
export const login = (email, password) => async (dispatch) => {
    try {
        dispatch({
            type: USER_LOGIN_REQUEST
        })
        //       headers
        const config = {
            //     token    
            headers: {
                'Content-type': 'application/json'
            },
        }
//     ,    
//      //1 - , 2 -     , 3 - headers, 
        const {data} = await axios.post('/api/users/login', {email, password}, config)
        //     

        //      data     userController  backend 
        dispatch({
            type: USER_LOGIN_SUCCESS,
            payload: data,
        })

        //     
        localStorage.setItem('userInfo', JSON.stringify(data))
} catch (error) {
    dispatch({
        type: USER_LOGIN_FAIL,
        payload: error.response && error.response.data.message ? error.response.data.message 
        : error.message
    })
    }
}


//    
export const logout = () => (dispatch) => {
    localStorage.removeItem('userInfo')
    dispatch({
        type: USER_LOGOUT,
    })
    dispatch({
        type: USER_DETAILS_RESET,
    })
    dispatch({
        type: ORDER_LIST_MY_RESET,
    })
    dispatch({
        type: USER_LIST_RESET,
    })
}


// 
export const register = (name, email, password) => async (dispatch) => {
    try {
        dispatch({
            type: USER_REGISTER_REQUEST
        })
        //       headers
        const config = {
            //     token    
            headers: {
                'Content-type': 'application/json'
            },
        }
//     ,    
//      //1 - , 2 -     , 3 - headers, 
        const {data} = await axios.post('/api/users', {name, email, password}, config)
        //     

        //      data     userController  backend 
        dispatch({
            type: USER_REGISTER_SUCCESS,
            payload: data,
        })
//    
        dispatch({
            type: USER_LOGIN_SUCCESS,
            payload: data,
        })

        //     
        localStorage.setItem('userInfo', JSON.stringify(data))
} catch (error) {
    dispatch({
        type: USER_REGISTER_FAIL,
        payload: error.response && error.response.data.message ? error.response.data.message 
        : error.message
    })
    }
}

//   
//     ; getState      userInfo       
export const getUserDetails = (id) => async (dispatch, getState) => {

    try {
        dispatch({
            type: USER_DETAILS_REQUEST
        })

// ,  userLogin  getState   userInfo  userLogin,    
        const {userLogin : {userInfo}} = getState()

        //       headers
        const config = {
            //     token    
            headers: {
                //'Content-type': 'application/json',
                Authorization: `Bearer ${userInfo.token}`,
            },
        }
//     ,    
//      //1 - , 2 -     , 3 - headers, 
        const {data} = await axios.get(`/api/users/${id}`, config)
        //     

        //      data     userController  backend 
        dispatch({
            type: USER_DETAILS_SUCCESS,
            payload: data,
        })
} catch (error) {
    dispatch({
        type: USER_DETAILS_FAIL,
        payload: error.response && error.response.data.message ? error.response.data.message 
        : error.message
    })
    }
}



